//import 
import React, { useState, useRef, useEffect, useCallback } from 'react';
import EXIF from 'exif-js';

import { GoogleMap, LoadScript, MarkerF } from '@react-google-maps/api'

//

//
import '../css/writeform.css';


const WirteFormOut = () => {

    const mapOptions = {
        zoomControl: true,
        mapTypeControl: false,
        streetViewControl: false,
        fullscreenControl: false,
    };

    //      
    const onLoad = useCallback((map) => {
        setMap(map);
        mapRef.current = map;
    }, []);

    //       
    const updateMarkerPosition = useCallback((exif) => {
        if (exif.lat && exif.lon) {
            const newPosition = {
                lat: exif.lat,
                lng: exif.lon
            };
            setMarkerPosition(newPosition);

            //      
            if (mapRef.current) {
                mapRef.current.panTo(newPosition);
                mapRef.current.setZoom(8);
            }
        }
    }, []);

    //   
    const containerStyle = {
        width: '100%',
        height: '200px',
        borderRadius: '8px'
    };

    /*--- -------------------------------*/

    /*--- (    )---*/
    const mapRef = useRef(null);
    const [map, setMap] = useState(null);
    const priceRef = useRef(null);
    const dateRef = useRef(null);
    const timeRef = useRef(null);

    const [photos, setPhotos] = useState([]);
    // 
    const [price, setPrice] = useState('');
    const [unit, setUnit] = useState('VND');
    const [way, setWay] = useState('');
    const [photo, setPhoto] = useState([]);

    const [center, setCenter] = useState({ lat: 37.7749, lng: -122.4194 }); //  

    //     state
    const [markerPosition, setMarkerPosition] = useState(center); //   

    /*---(useEffect)+(handle)-------*/
    useEffect(() => {
        priceRef.current?.focus();


        //    
        if (navigator.permissions && navigator.permissions.query) {
            navigator.permissions.query({ name: 'geolocation' })
                .then(function (result) {
                    if (result.state === 'denied') {
                        alert("  .-");
                    }
                });
        }

        //    
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(
                (position) => {
                    const { latitude, longitude } = position.coords;
                    setCenter({ lat: latitude, lng: longitude });
                    setMarkerPosition({ lat: latitude, lng: longitude });
                },
                (error) => {
                    console.error('Geolocation error:', error.message);
                    switch (error.code) {
                        case error.PERMISSION_DENIED:
                            alert("   .");
                            break;
                        case error.POSITION_UNAVAILABLE:
                            alert("    .");
                            break;
                        case error.TIMEOUT:
                            alert("    .");
                            break;
                    }
                }
            );
        } else {
            alert("     .");
        }
    }, [])

    //   box  
    const handleBoxPriceInfo = () => {
        priceRef.current?.focus();
    };

    //       
    const handlePrice = (e) => {
        const priceEl = e.target;
        const price = priceEl.value;

        const textWidth = price.length * 17; //  :  17px 
        priceEl.style.width = `${textWidth}px`; //    
        console.log(price, unit, way)

        setPrice(price);
    };

    const handleUnit = (item) => {
        setUnit(item);
        console.log(price, unit, way)
    };

    const handleWay = (item) => {
        setWay(item);
        console.log(price, unit, way)
    };

    const handlePhotoUp = (e) => {
        const files = e.target.files; //  
        const newPhotos = [];

        Array.from(files).forEach((file) => {
            const exifVo = getExif(file);

            const newPhoto = {
                file: file,
                preview: URL.createObjectURL(file),
                exif: exifVo,
            };

            console.log(newPhoto)
            newPhotos.push(newPhoto); //   
        });

        // 
        setPhotos((prevPhotos) => { return [...prevPhotos, ...newPhotos] });

    };

    const handleXyt = async (index) => {
        try {
            const exifData = await getExif(photos[index].file);
            
            console.log(exifData);
            setPhoto(photos[index]);
    
            dateRef.current.innerText = exifData.date || ' ';
            timeRef.current.innerText = exifData.time || ' ';
    
            //     
            if (exifData.lat && exifData.lon) {
                updateMarkerPosition(exifData);
            } else {
                console.warn('   .');
            }
        } catch (error) {
            console.error('EXIF    :', error);
        }
    };

    const handlePhotoDel = (index) => {
        setPhotos(prevPhotos => prevPhotos.filter((_, i) => i !== index));
    };

    return (

        <div className="container">
            <header> </header>

            <main id="outItemAdd">
                <div className="area-main-body">
                    <div className="box-priceInfo" onClick={handleBoxPriceInfo} >
                        <div className="question"> ?</div>
                        <div className="wrap-price">
                            <input type="number" className="txt-price" placeholder="0"
                                value={price}
                                onChange={handlePrice}
                                ref={priceRef}
                            />
                            <span className="unit">{unit}</span>
                            <span className="way">{way}</span>
                        </div>
                        <div className="basc-price">{price * 1400} KRW</div>

                        <div class="box-unit-way">
                            <div className="box-option">
                                <div className="title">
                                    
                                </div>
                                <ul className="wrap-unit">
                                    {['USD', 'KRW', 'VND', 'THB'].map((item) => {
                                        return (
                                            <li key={item}>
                                                <button className={(unit === item) ? "btn btn-pri btn-unit" : "btn btn-unit"} onClick={() => { handleUnit(item) }}>{item}</button>
                                            </li>
                                        )
                                    })}
                                </ul>
                            </div>
                            <div className="box-option">
                                <div className="title">
                                    
                                </div>
                                <ul className="wrap-way">
                                    {['', ''].map((item) => {
                                        return (
                                            <li>
                                                <button className={(way === item) ? "btn btn-line btn-way" : "btn btn-way"} onClick={() => { handleWay(item) }}>{item}</button>
                                            </li>
                                        )
                                    })}
                                </ul>
                            </div>
                        </div>
                    </div>
                    <div className="box-itemPhoto">
                        <div className="wrap-title-btn">
                            <div className="title"></div>
                            <div className="wrap-btn">
                                <label className="btn btn-s" htmlFor="file-photo"></label>
                                <input id="file-photo" className="blind" type="file" accept="image/*" name="" multiple
                                    onChange={handlePhotoUp}
                                />
                            </div>
                        </div>
                        <ul>
                            {photos.map((item, index) => (
                                <li key={index}>
                                    <button type="button" className={(photo === item) ? "btn-xyt active" : "btn-xyt"} onClick={() => { handleXyt(index); }}></button>
                                    <button type="button" className="btn-del" onClick={() => { handlePhotoDel(index); }}>X</button>
                                    <img className="itemPhoto" src={item.preview} alt="Uploaded" />
                                </li>

                            ))}
                        </ul>
                    </div>
                    <div className="box-mapTime">
                        <div className="wrap-map-time">
                            <div className="title"></div>
                            <div className="wrap-datetime">
                                <button type="button" className="btn btn-s btn-datetime" ref={dateRef}>2024-12-22</button>
                                <button type="button" className="btn btn-s btn-datetime" ref={timeRef}>09:10</button>
                            </div>
                        </div>


                        <LoadScript googleMapsApiKey={process.env.REACT_APP_GOOGLE_MAPS_API_KEY}>
                            <GoogleMap
                                mapContainerStyle={containerStyle}
                                center={markerPosition}
                                zoom={6}
                                options={mapOptions}
                                onLoad={onLoad}
                            >
                                <MarkerF
                                    position={markerPosition}
                                    animation={window.google?.maps.Animation.DROP}
                                />
                            </GoogleMap>
                        </LoadScript>

                    </div>
                    <div className="box-titleMemo">
                        <div className="wrap-memo">
                            <div className="title"></div>
                        </div>
                        <input type="text" name="" />
                        <textarea
                            className="content"
                            placeholder=" "
                        />
                    </div>
                    <div className="box-btnCtr">
                        <button className="btnAdd btn btn-pri"> </button>
                    </div>
                </div>


            </main>


        </div>
    );
}

/*---  -----------------------------*/
//exif 
function getExif(photo) {
    return new Promise((resolve, reject) => {
        const exifVo = {
            lat: null,
            lon: null,
            datetime: null,
            date: null,
            time: null,
        };

        EXIF.getData(photo, function () {
            const latitude = EXIF.getTag(this, 'GPSLatitude');
            const longitude = EXIF.getTag(this, 'GPSLongitude');
            const latitudeRef = EXIF.getTag(this, 'GPSLatitudeRef');
            const longitudeRef = EXIF.getTag(this, 'GPSLongitudeRef');

            exifVo.lat = latitude ? convertDMSToDD(latitude, latitudeRef) : null;
            exifVo.lon = longitude ? convertDMSToDD(longitude, longitudeRef) : null;


            //    
            if (exifVo.datetime) {
                exifVo.date = exifVo.datetime.split(' ')[0].replaceAll(':', '-'); // "YYYY-MM-DD"
                exifVo.time = exifVo.datetime.split(' ')[1].slice(0, 5); // "HH:MM:SS"
            }


            resolve(exifVo)
        });
    });
}


// 
function convertDMSToDD(dms, direction) {
    const [degrees, minutes, seconds] = dms;
    let dd = degrees + minutes / 60 + seconds / 3600;
    if (direction === 'S' || direction === 'W') {
        dd = dd * -1; //      
    }
    return dd;
};

export default WirteFormOut;
