import React, { useState, useRef, useEffect, useCallback } from 'react';
import EXIF from 'exif-js';
import { GoogleMap, LoadScript, MarkerF } from '@react-google-maps/api';

const Test2 = () => {
  const mapOptions = {
    zoomControl: true,
    mapTypeControl: false,
    streetViewControl: false,
    fullscreenControl: false,
  };

  const containerStyle = {
    width: '100%',
    height: '200px',
    borderRadius: '8px',
  };

  const mapRef = useRef(null);
  const priceRef = useRef(null); // priceRef 
  const dateRef = useRef(null);
  const timeRef = useRef(null);
  
  const [map, setMap] = useState(null);
  const [photos, setPhotos] = useState([]);
  const [price, setPrice] = useState('');
  const [unit, setUnit] = useState('VND');
  const [way, setWay] = useState('');
  const [photo, setPhoto] = useState([]);
  const [center, setCenter] = useState({ lat: 37.7749, lng: -122.4194 });
  const [markerPosition, setMarkerPosition] = useState(center);

  const onLoad = useCallback((map) => {
    setMap(map);
    mapRef.current = map;
  }, []);

  const updateMarkerPosition = useCallback((exif) => {
    if (exif.lat && exif.lon) {
      const newPosition = { lat: exif.lat, lng: exif.lon };
      setMarkerPosition(newPosition);
      if (mapRef.current) {
        mapRef.current.panTo(newPosition);
        mapRef.current.setZoom(8);
      }
    }
  }, []);

  useEffect(() => {
    priceRef.current?.focus(); //   
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { latitude, longitude } = position.coords;
          setCenter({ lat: latitude, lng: longitude });
          setMarkerPosition({ lat: latitude, lng: longitude });
        },
        (error) => {
          console.error('Geolocation error:', error.message);
          switch (error.code) {
            case error.PERMISSION_DENIED:
              alert("   .");
              break;
            case error.POSITION_UNAVAILABLE:
              alert("    .");
              break;
            case error.TIMEOUT:
              alert("    .");
              break;
          }
        }
      );
    } else {
      alert("     .");
    }
  }, []);

  const handlePhotoUp = (e) => {
    const files = e.target.files;
    const newPhotos = [];
    Array.from(files).forEach((file) => {
      getExif(file).then(exifVo => {
        const newPhoto = { file: file, preview: URL.createObjectURL(file), exif: exifVo };
        newPhotos.push(newPhoto);
        if (exifVo.lat && exifVo.lon) updateMarkerPosition(exifVo);
      });
    });
    setPhotos((prevPhotos) => [...prevPhotos, ...newPhotos]);
  };

  return (
    <div className="container">
      <header> </header>
      <main id="outItemAdd">
        <div className="area-main-body">
          <div className="box-priceInfo" onClick={() => priceRef.current?.focus()}>
            <div className="question"> ?</div>
            <div className="wrap-price">
              <input type="number" className="txt-price" placeholder="0" value={price} onChange={(e) => setPrice(e.target.value)} ref={priceRef} />
              <span className="unit">{unit}</span>
              <span className="way">{way}</span>
            </div>
            <div className="basc-price">{price * 1400} KRW</div>
            <div className="box-unit-way">
              <div className="box-option">
                <div className="title"></div>
                <ul className="wrap-unit">
                  {['USD', 'KRW', 'VND', 'THB'].map((item) => (
                    <li key={item}>
                      <button className={(unit === item) ? "btn btn-pri btn-unit" : "btn btn-unit"} onClick={() => setUnit(item)}>{item}</button>
                    </li>
                  ))}
                </ul>
              </div>
              <div className="box-option">
                <div className="title"></div>
                <ul className="wrap-way">
                  {['', ''].map((item) => (
                    <li key={item}>
                      <button className={(way === item) ? "btn btn-line btn-way" : "btn btn-way"} onClick={() => setWay(item)}>{item}</button>
                    </li>
                  ))}
                </ul>
              </div>
            </div>
          </div>

          <div className="box-itemPhoto">
            <div className="wrap-title-btn">
              <div className="title"></div>
              <div className="wrap-btn">
                <label className="btn btn-s" htmlFor="file-photo"></label>
                <input id="file-photo" className="blind" type="file" accept="image/*" multiple onChange={handlePhotoUp} />
              </div>
            </div>
            <ul>
              {photos.map((item, index) => (
                <li key={index}>
                  <img className="itemPhoto" src={item.preview} alt={`Uploaded ${index}`} />
                  {/*    */}
                  {item.exif.lat && item.exif.lon && (
                    <>
                      <p>: {item.exif.lat.toFixed(6)}</p>
                      <p>: {item.exif.lon.toFixed(6)}</p>
                    </>
                  )}
                </li>
              ))}
            </ul>
          </div>

          <LoadScript googleMapsApiKey={process.env.REACT_APP_GOOGLE_MAPS_API_KEY}>
            <GoogleMap
              mapContainerStyle={containerStyle}
              center={markerPosition}
              zoom={8}
              options={mapOptions}
              onLoad={onLoad}
            >
              {markerPosition && (
                <MarkerF position={markerPosition} animation={window.google?.maps.Animation.DROP} />
              )}
            </GoogleMap>
          </LoadScript>

          {/*    */}
          <textarea className="content" placeholder=" " />
        </div>

        {/*   */}
        <button className="btnAdd btn btn-pri"> </button>
      </main>
    </div>
  );
};

// EXIF    
function getExif(photo) {
  return new Promise((resolve, reject) => {
    EXIF.getData(photo, function () {
      try {
        const latitude = EXIF.getTag(this, 'GPSLatitude');
        const longitude = EXIF.getTag(this, 'GPSLongitude');
        const latitudeRef = EXIF.getTag(this, 'GPSLatitudeRef');
        const longitudeRef = EXIF.getTag(this, 'GPSLongitudeRef');
        
        let latDecimal = null;
        let lonDecimal = null;

        if (latitude && longitude && latitudeRef && longitudeRef) {
          latDecimal = convertDMSToDD(latitude, latitudeRef);
          lonDecimal = convertDMSToDD(longitude, longitudeRef);
        }

        resolve({
          lat: latDecimal,
          lon: lonDecimal,
          datetime: EXIF.getTag(this, 'DateTime'),
          date: EXIF.getTag(this, 'DateTimeOriginal'),
          time: EXIF.getTag(this, 'DateTimeDigitized')
        });
      } catch (error) {
        reject(error);
      }
    });
  });
}

function convertDMSToDD(dmsArray, direction) {
  if (!dmsArray || dmsArray.length !== 3) return null;

  let degrees = dmsArray[0];
  let minutes = dmsArray[1];
  let seconds = dmsArray[2];

  let dd =
    degrees + minutes / 60 + seconds / (60 * 60);

  if (direction === 'S' || direction === 'W') dd *= -1;

  return dd;
}

export default Test2;